nav{
    width: 100vw;
    height: 5rem;
    display: grid;
    place-items: center;
    background: var(--color-primary);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    box-shadow: 3px 5px 10px var(--color-bg);
    transition: var(--transition);
}

.nav__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__logo{
    width: 2.5rem;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 1px 1px 5px grey;
}

.nav__menu{
    display: flex;
    gap: 3.5rem;
}

#theme__icon{
    background: transparent;
    color: var(--color-white);
    font-size: 2rem;
    cursor: pointer;
}

/*tablets*/

@media screen and (max-width: 1024px){
    nav{
        display: none;
    }
}